import React from 'react';
import PropTypes from 'prop-types';
import './SeatSelected.css';

/**
 * deac: 底座组件
 */

const SeatSelected = ({selected, onRemove}) => {
  // if(!selected.length){
  //   return null;
  // }
  return (
    <div className="seatSelected">      
      <ul className="seatSelected__list">
      { 
        selected.map(seat => (
          <li key={seat.id} className="seatSelected__item moiveTicket">
            <div className="moiveTicket__detail">
              <div className="moiveTicket__pos">{seat.rowIndex}排{seat.colIndex}做</div>
              <div className="moiveTicket__price">  <i className="moiveTicket__tag">卡</i> 33元</div>
            </div>
            <div className="moiveTicket__close" onClick={() => onRemove(seat.id)} />
          </li>
        ))
      }
      </ul>
      <div className="seatSelected__buy">{33 * selected.length}元 确认选座</div>
    </div>
  );
};

SeatSelected.propTypes = {
  selected: PropTypes.array.isRequired,
  onRemove: PropTypes.func.isRequired,
};

export default SeatSelected;


