import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import RenderToBody from '../../components/RenderToBody';
import TabMenu from '../../components/TabMenu'
import TopBar from './components/TopBar';
import Slide from './components/Slide';
import MovieItem from './components/MovieItem';
import CityLayer from './components/CityLayer';
import request from '../../helpers/request';
import './index.css'

class index extends Component {
    state = { 
        city: '', //当前城市
        poster: [], //slide海报
        movie: [],  //当前热映电影
        cityLayerVisble: false //城市浮层是否展现
    }

    hideCityLayer = () =>{
        this.setState({
            cityLayerVisble: false
        })
    }
    
    onChangeCity = (city) => {
        this.setState({
            city,
        })
        this.hideCityLayer();
    }

    showCityLayer = () =>{
        this.setState({
            cityLayerVisble: true
        })
    }
    
    componentDidMount () {
        this.getData();
        // this.getCity();
    }
    getData = async () =>{
        const data = await request('/index');
        const {city, poster, movie} = data;
        this.setState({
            city: city,
            poster,
            movie,
        })
        console.log(data);
    }
    getCity = async () =>{
        const data = await request('/city')
        // console.log(data);
    }
    render() {
        const { city, poster, movie, cityLayerVisble} = this.state;

        return (
            <div className="home">
               <TopBar city={city} showCityLayer={this.showCityLayer}/>
               <div className="home__slide">
                    <div className="home__slideWrap">
                        <Slide data={poster} />
                    </div>                    
               </div>
               <ul className="home__content">
                    {movie.map((item, index) => <li key={item.name}><Link to="/detail"><MovieItem data={item}/></Link></li>)}
               </ul>    
               <TabMenu current="movie"/>   
               {cityLayerVisble && <RenderToBody><CityLayer onClose={this.hideCityLayer} onSelect={this.onChangeCity} /></RenderToBody>}        
            </div>
        );
    }
}

index.propTypes = {

};

export default index;


