import React, { Component } from 'react';
import PropTypes from 'prop-types';
import MovieInfo from './components/MovieInfo';
import SeatSelected from './components/SeatSelected';
import SeatSelector from './container/SeatSelector'
import {Link} from 'react-router-dom';
import './index.css'

class index extends Component {
  state = {
    selectSeat: []
  }

  addSeat = (seat) =>{
    this.setState(prevState => ({
      selectSeat: [...prevState.selectSeat, seat]
    }));
  }

  removeSeat = (id) =>{
    this.setState({
      selectSeat: this.state.selectSeat.filter(seat => seat.id !== id)
    })
  }
  render() {
    const {selectSeat} = this.state;
    return (
      <div className="seat">
        <div className="tOperator">
          {/* <Link to="/detail"> */}
            <div className="tOperator__icon tOperator__icon--blackBack" onClick={() => {window.history.back()}} />
          {/* </Link>           */}
            万达影院
          <i className="tOperator__icon tOperator__icon--blackShare" />
        </div>
        <MovieInfo />
        <div className="seat__main">
          <div className="seat__tip"></div>
          <div className="seat__graph">
            <div className="seat__screen">B13屏幕</div>
            <div className="seat__map">
              <SeatSelector selected={selectSeat} onRemove={this.removeSeat} onAdd={this.addSeat}/>
            </div>            
          </div>
        </div>
        <SeatSelected selected={selectSeat} onRemove={this.removeSeat} onAdd={this.addSeat} />
      </div>
    );
  }
}

index.propTypes = {

};

export default index;


