import React, { Component } from 'react';
import PropTypes from 'prop-types';
import request from '../../../helpers/request';
import ScoreDistribute from '../components/ScoreDistribute';
import TagList from '../components/TagList';
import CommentList from '../components/CommentList';
import './Comment.css';
/**
 * 详情页评价主要内容
 * ScoreDistribute 星星评分
 * TagList 标签样式
 * CommentList 评价详情列表
 */
class Comment extends Component {
  state = {
    tag: [],         //标签
    comments: [],    //评论
    current: ''     //当前选中的标签
  }

  componentDidMount(){
    this.getData()
  }

  getData = async () =>{
    const data = await request('/comment');
    this.setState({
      tag: data.tags,
      comments: data.list,
      current: data.tags[0] ? data.tags[0].text : 0
    })
  }
  changeTag = value => {
    this.setState({
      current: value
    })
  }

  toggleZan = (id) => {
    this.setState((prevState) => ({
      comments: prevState.comments.map(item => {
        if(item.id === id){
          return {
            ...item,
            isZan: !item.isZan,
            zan: item.isZan ? --item.zan : ++item.zan
          }
        }else{
          return {...item}
        }
      })
    }))
  }

  render() {
    const {tag, comments, current} = this.state;
    const filterComments = comments.filter(comment => comment.tag === current);
    return (
      <div className="myComment">
        <ScoreDistribute />
        <div style={{marginTop: '16px'}}>
          <TagList tag={tag} current={current} onClick={this.changeTag}/>
        </div>        
        <div style={{marginTop: '16px'}} >
          <CommentList comments={filterComments} onClickZan={this.toggleZan}/>
        </div>        
      </div>
      
    );
  }
}

Comment.propTypes = {

};

export default Comment;


